
import moment from 'moment'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default{
mixins: [common],
filters:{
avatar (path) {
return path === '' ? 'http://s1.xiao360.com/common_img/avatar.jpg' : path
}
},
components: {
XButton,
Divider,
Group,
Cell,
XInput,
},
data () {
return {
students: [],
active_id: 0,
active_name: '',
}
},
mounted () {
this.get_students().then(data=>{
let user_info = this.$store.state.user.info

this.students = data
if(user_info.default_sid !== 0){
this.select_student(user_info.students.find(s=>s.sid===user_info.default_sid))
} else if(user_info.default_sid===0 && this.students.length > 0) {
this.select_student(this.students[0])
}
})
},
methods: {
select_student (student) {
let uri = util.sprintf('users/switch_sid?uid=%s&sid=%s',this.$store.state.user.info.uid,student.sid)
this.$rest(uri).get()
.success(data=>{
this.active_id = student.sid
this.active_name = student.student_name
this.$store.commit('setArchiveInfo',student.sid)
}).error(response=>{
this.alert('错误提示',response.body.message,true)
})
}
},
computed: {
showAdd () {
return !this.students||this.students.length===0
},
edit_archive () {
return './add?sid=' + this.active_id
}
}
}



